<template>
  <div class="answer-someone-container">
    <el-card class="header-card">
      <h3>üìö Á´†ËäÇÈóÆÈ¢òÂàóË°®</h3>
      <template v-if="questions.length">
        <el-table 
          :data="questions" 
          stripe 
          style="width: 100%"
          @row-click="handleQuestionClick"
        >
          <el-table-column 
            prop="questionContent" 
            label="ÈóÆÈ¢òÂÜÖÂÆπ"
            :show-overflow-tooltip="true"
          ></el-table-column>
          <el-table-column
            prop="createTime"
            label="ÊèêÈóÆÊó∂Èó¥"
            width="180"
          ></el-table-column>
        </el-table>
      </template>
      <div v-else class="empty-state">
        <el-empty description="ÂΩìÂâçÁ´†ËäÇËøòÊ≤°ÊúâÂÖ∂‰ªñÂêåÂ≠¶ÁöÑÈóÆÈ¢òÂíåÂõûÁ≠î">
          <el-button 
            type="primary" 
            @click="handleSelectChapter"
            class="select-chapter-btn"
          >
            <i class="el-icon-folder-opened"></i>
            ÈÄâÊã©ÂÖ∂‰ªñÁ´†ËäÇ
          </el-button>
        </el-empty>
      </div>
    </el-card>
  </div>
</template>



<script setup>
import { useRouter } from 'vue-router'

const router = useRouter()

// Êñ∞Â¢ûÁÇπÂáªÂ§ÑÁêÜ
const handleQuestionClick = (row) => {
  router.push({
    name: 'QuestionDetail',
    params: { questionId: row.id }  // Á°Æ‰øù‰º†ÈÄíÊï∞ÂÄºÂûãID
  })
}
import { ref, onMounted } from 'vue'
// import { useRoute } from 'vue-router'
import { listQuestions } from '@/api/course'
import { inject } from 'vue'
const appState = inject('appState')

// ‰øÆÊîπsetupÈÉ®ÂàÜ
// const route = useRoute()
const questions = ref([])

onMounted(async () => {
  try {
    const res = await listQuestions({
      courseId: Number(appState.currentCourseId),
      chapterId: Number(appState.currentChapterId)
    })
    if (res.data?.code === 1) {
      questions.value = res.data.data || []
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÈóÆÈ¢òÂàóË°®Â§±Ë¥•:', error)
  }
})

// Êñ∞Â¢ûÁ´†ËäÇÈÄâÊã©ÊñπÊ≥ï
const handleSelectChapter = () => {
  router.push({ name: 'chapters' }) // Á°Æ‰øùË∑ØÁî±ÈÖçÁΩÆ‰∏≠ÊúâËøô‰∏™ÂêçÁß∞
}
</script>

<style scoped>
